import React, { FC } from "react";
import * as LucideIcon from "lucide-react";
import styled from "styled-components";

import { LegacyButtonVariants } from "./legacyButtonVariants";
import { returnVariant } from "./utils";

export type IconName = keyof typeof LucideIcon;

interface StyledIconProps {
	variant?: LegacyButtonVariants
}

const StyledIcon = styled.div<StyledIconProps>`
	width: auto;
	height: auto;
	max-height: 18.8px;
	margin: 0 6.4px;
	fill: ${({ variant }) => variant && returnVariant(variant).color};
`;

interface IconProps extends StyledIconProps {
	iconColor?: string
	size?: number
	stroke?: number
	icon?: IconName
	customIconColor?: boolean
}

const Icon: FC<IconProps> = ({
	variant = "default",
	icon,
	iconColor,
	size,
	stroke,
	customIconColor,
}) => {
	const IconComponent = LucideIcon[icon as IconName] as FC<{
		color?: string;
		size?: number;
		strokeWidth?: number;
	}>;
	const getIconColor = customIconColor
		? iconColor
		: returnVariant(variant).iconColor;

	return (
		<StyledIcon variant={variant}>
			<IconComponent
				color={getIconColor}
				size={size}
				strokeWidth={stroke}
			/>
		</StyledIcon>
	);
};

export default Icon;
